.block-item { background: #e5f6fe; border: 1px solid #0f294f; position: relative; padding: 25px 20px 25px 0; }

@media only screen and (max-width: 767px) { .block-item { padding: 25px 10px; } }

.block-item__logo { max-width: 147px; position: absolute; top: -37%; left: 20px; display: block; }

@media only screen and (max-width: 1023px) { .block-item__logo { position: relative; display: block; left: 50%; transform: translateX(-50%); } }

.block-item__logo img { max-width: 100%; display: block; }

.block-item__text { font-size: 15px; font-weight: 400; line-height: 1.59; margin-left: 185px; max-width: 270px; letter-spacing: -0.02em; color: #677482; }

@media only screen and (max-width: 1023px) { .block-item__text { max-width: 100%; margin-left: 0; text-align: center; } }

@media only screen and (max-width: 767px) { .block-item__text { font-size: 10px; } }

.select .selection { text-align: left; }

.select2-selection .select2-selection--single { font-size: 14px; font-weight: 400; line-height: 1.2; padding: 11px 15px 8px; color: #677482; width: 100%; background-color: #F6F9FC; border-radius: 4px; outline: none; }

.select2-container--default .select2-selection--multiple, .select2-container--default .select2-selection--single { border: none; font-size: 14px; font-weight: 400; width: 100%; background-color: #F6F9FC; border-radius: 4px; outline: none; }

.select2-container--default .select2-selection--single .select2-selection__rendered { color: #677482; line-height: 37px; }

.select2-search--dropdown { display: none; }

.select2-container .select2-selection--single { height: 37px; border: 1px solid #F6F9FC; }

.select2-container--default .select2-selection--single .select2-selection__arrow { height: 35px; right: 10px; }

.select2-container--default .select2-selection--single .select2-selection__arrow b { background: url("../img/arrow-bottom.svg") no-repeat; border-color: transparent; border-style: none; width: 10px; height: 7px; background-size: contain; }

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b { transform: rotate(180deg); }

.select2-dropdown { color: #677482; font-size: 14px; font-weight: 400; overflow: hidden; border: 1px solid #677482; border-radius: 4px; }

.select2-container--default .select2-results__option--highlighted[aria-selected] { background-color: #fff; color: #677482; font-size: 14px; font-weight: 400; }

.select2-container--default .select2-results__option[aria-selected=true] { background-color: #335578; color: #fff; }

.select2-container .select2-selection--single .select2-selection__rendered { padding-left: 15px; }

.select2-container--default.select2-container--open.select2-container--below .select2-selection--multiple, .select2-container--default.select2-container--open.select2-container--below .select2-selection--single { border: 1px solid #677482; border-bottom: none; }

.select2-results__option { padding-left: 15px; }

.select2-container--default.select2-container--open.select2-container--above .select2-selection--single { border: 1px solid #677482; border-top: none; }
